/* ELEMENT STYLES -------------------- */
body {
  background: #e8e7e7; 
  text-transform: uppercase;
  font-weight: 400;
  margin-top: -4rem;
}

header {
  background-image: url('https://thumbs.dreamstime.com/b/happy-halloween-banner-party-invitation-background-happy-halloween-banner-background-tree-grass-spooky-ghost-halloween-199806790.jpg');
  color: white;
  padding: 5px 10px;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  background-color: orange;
  border-bottom: solid 2px #D4CAFF;
  font-size: 1.2rem;
  background-position: 95% 85%;
}

header h1 {
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: 1.5em;
  letter-spacing: 2px;
  font-weight: bold;
}

/* FORMS */
form {
  display: -ms-flexbox;
  display: flex;
  background-color: orange;
}

input[type=text],
input[type=url] {
  -ms-flex-positive: 1;
      flex-grow: 1;
  border-width: 0 0 1px 0;
  margin: 15px 10px 15px 15px;
  padding: 10px;
  border-radius: 5px;
  background-color: lightgray;
  border-style: none;
  text-shadow: none;
  text-transform: uppercase;
  color: yellow;
  letter-spacing: 2px;
  outline: none;
}

input[type=text]::-webkit-input-placeholder,
input[type=url]::-webkit-input-placeholder {
  color: black;
  letter-spacing: 2px;
}

input[type=text]:focus,
input[type=url]:focus
{
  background-color: rgba(0, 0, 0, 0.2);
}

input[type=submit] {
  display: block;
  font-size: .6em;
  margin: 15px 15px 15px 0; 
  padding: 10px;
  background-color: black;
  border-radius: 5px;
  border: none;
  color: #fff;
  letter-spacing: 2px;
  font-weight: bold;
  text-shadow: none;
  text-transform: uppercase;
  cursor: pointer;
}

button, 
input[type=submit] {
  outline: none;
  -webkit-transition: background-color 0.2s ease,
              color 0.2s ease;
  -o-transition: background-color 0.2s ease,
              color 0.2s ease;
  transition: background-color 0.2s ease,
              color 0.2s ease;
}

/* ICONS */
svg {
  width: 50px;
  height: 27px;
  margin-right: 5px;
  fill: #F0F0F0;
}

.is-high-score {
  fill: #FFC657;
  -webkit-animation: grow 10s ease-in-out;
          animation: grow 10s ease-in-out;
  -webkit-transform-origin: 100% 100%;
      -ms-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
}

@-webkit-keyframes grow {
  50% { -webkit-transform: scale(1.4) rotate(-3deg); transform: scale(1.4) rotate(-3deg); }
}

@keyframes grow {
  50% { -webkit-transform: scale(1.4) rotate(-3deg); transform: scale(1.4) rotate(-3deg); }
}

/* COMPONENT STYLES -------------------- */
.scoreboard {
  background: #fff;
  margin: 70px auto;
  -webkit-box-shadow: 0 2px 0 gray;
          box-shadow: 0 2px 0 gray;
  border-radius: 6px;
  overflow: hidden;
}

.player {
  display: -ms-flexbox;
  display: flex;
  font-size: 1.2em;
  border-bottom: solid 2px #EEEEEE;
  letter-spacing: 2px;
}

  .player:last-of-type {
    border-bottom-color: #D4CAFF;
  }

  .player-name:hover .remove-player {
    visibility: visible;
  }

  .player-name,
  .counter-score {
    color: black;
    /* border: 100% double maroon; */
    /* text-indent: -2em; */
  }
  
  .counter-score {
    border: .25rem inset rgb(237, 231, 228);
    /* text-indent: -15em; */
    margin-bottom: -1rem;
  }

  .player-name {
    -ms-flex-positive: 1;
        flex-grow: 1;
    /* line-height: 3.5em; */
    padding-left: 10px;
    font-size: 3rem;
    font-family: "Creepster";
  }

