    font-size: .33em;
    border: none;
    outline: none;
    visibility: hidden;
    color: #EF5350;
    cursor: pointer;
    margin: 0 20px 0 0;
    padding: 0;
    background: none;
  } */

.stats,
.stopwatch {
  width: 300px;
}

.stats {
  display: -ms-flexbox;
  display: flex;
  margin: 0 0 0 28px;
  font-weight: bold;
  font-size: 2rem;
}

  .stats td:first-child {
    text-align: right;
    font-weight: normal;
    letter-spacing: 2px;
    font-size: .7em;
  }

  .stats td:last-child {
    text-align: left;
  }

.stopwatch {
  padding: 15px 10px 5px 10px;
  margin: -5px -10px -5px 10px;
  background: #E55B13;
}

  .stopwatch-time {
    display: block;
    font-family: monospace;
    font-size: 2.15em;
  }

  .stopwatch button {
    margin: 8px 5px;
    background-color: black;
    border-radius: 5px;
    padding: 7px 8px;
    border: none;
    color: #fff;
    letter-spacing: 2px;
    font-weight: bold;
    text-shadow: none;
    text-transform: uppercase;
  }

  .stopwatch button:hover,
  input[type=submit]:hover {
    background: #fff;
    color: black;
    cursor: pointer;
  }

  .stopwatch h2 {
    font-size: .6em;
    margin: 0;
    font-weight: normal;
    letter-spacing: 2px;
  }

.counter {
  display: -ms-flexbox;
  display: flex;
  width: 190px;
}

  .counter-action {
    border: none;
    font-size: 1.15em;
    font-weight: bold;
    color: #B0AEBA;
    background-color: #DCDCDC;
    display: block;
    padding: 20px 20px;
    cursor: pointer;
    outline: none;
  }

  .counter-action:hover {
    color: #fff;
  }

  .increment:hover {
    background: #83D97C;
  }

  .decrement:hover {
    background: #EF5350;
  }

  .counter-score {
    -ms-flex-positive: 1;
        flex-grow: 1;
    text-align: center;
    font-family: monospace;
    font-size: 3em;
    line-height: 1em;
  }

  .logo {
    max-width: 42%;
    height: auto;
    /* box-shadow: 0 2px 0 gray; */
    /* border: .15rem inset rgb(162, 162, 179); */
-webkit-backdrop-filter: blur(1.5px);
        backdrop-filter: blur(1.5px);  

}

.btn {
      font-size: 2.5rem;
text-indent: 5rem;
}
